<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    {{dollors}}
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      dollors:'',
      test:''
    }
  },
  methods:{
    dotoStore(message){
      store.commit("toMessage", message);
    },
    doFoodGogo(name, time, success) {
      return new Promise((resolve, reject)=>{
        if (success) {
           setTimeout(()=>{
             resolve(`${name}花了${time/1000}完成`);
           },time)
        } else {
          reject(`${name}GG了`);
        }
      })
    }
  }, 
  mounted(){

  }

}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



