<template>
  <div class="hello">
    <h1 class="title">{{ msg }}</h1>
      <a-button  type="primary" v-on:click="get(args)">全部</a-button>
      <select v-model="selected" v-on:change="get(setarg(selected))">
        <option  v-for="(item, i) in map" :key="i">{{item}}</option>
      </select>
      <div v-html="view"></div>
  </div>
</template>

<script>

import "@/assets/css/style.css";
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '一般天氣預報-今明 36 小時天氣預報',
      url: 'https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-C0032-001',
      title: '',
      view: '',
      map: [ "宜蘭縣", "花蓮縣", "臺東縣", "澎湖縣", "金門縣", "連江縣", "臺北市", "新北市", "桃園市", "臺中市", "臺南市", "高雄市", "基隆市", "新竹縣", "新竹市", "苗栗縣", "彰化縣", "南投縣", "雲林縣", "嘉義縣", "嘉義市", "屏東縣" ],
      selected: '',
      args:{
        params:{
                Authorization:'CWB-2CB29FEC-6454-4BC6-B52A-5F9ED4542131'
        }
      },
      args1:{
        params:{
                Authorization:'CWB-2CB29FEC-6454-4BC6-B52A-5F9ED4542131'
        }
      }
    }
  }, 
  
  methods:{
    setarg(id){
      this.args1["params"]["locationName"] = id;
      return this.args1;
    },
    get(args) {
      this.$axios.get(this.url, args)
      .then(result=>{
        return result.data;
      })
      .then(data=>{
        if (data.success == "true") {
          return data.records;
        } else {
          throw data.success;
        }
      })
      .then(data=>{
        this.title = data.datasetDescriptionl;
        return data.location;
      })
      .then(data=>{
        this.view = '';
        for (const key in data) {
          if (data.hasOwnProperty(key)) {
            const element = data[key];
            console.log(element.locationName);
            console.log(element.weatherElement);
            this.view += this.signle(element.locationName, element.weatherElement);
          }
        }
      })
      .catch(error=>{console.log('error', error);})
    },
    signle:function(title, data) {
      let list = {};
      for (const typekey in data) {
          for (const k in data[typekey].time) {
            let elName = data[typekey].elementName;
            let obj = {
                        startTime: data[typekey].time[k].startTime,
                        parameterName: data[typekey].time[k].parameter.parameterName,
                        endTime: data[typekey].time[k].endTime
                      };
            if (elName == "Wx") {
              obj['value'] = data[typekey].time[k].parameter.parameterValue;
            }          
            if (typeof(list[k]) == 'undefined') {
                list[k] = {};
            }
            list[k][elName] = obj;
          }       
      }
      // console.log(list);
      let allHTML = '';
      allHTML += `<header><h1>${title}</h1></header>`;
      allHTML += `<ul class="grid-container">`;
      for (const key in list) {
          let temp = list[key].MinT.parameterName+'<sup>°</sup> - '+ list[key].MaxT.parameterName+'<sup>°</sup>';
          let time = list[key].MinT.startTime +"<br>" +list[key].MinT.endTime;
          let location = list[key].Wx.parameterName;
          let type = '';
          let icon = '';
          switch (list[key].Wx.value) {
            case "1": type = 'card-sunny'; icon = 'sunny'; break;
            case "2": type = 'card-snow';  icon = 'snow'; break;
            case "3": type = 'card-snow';  icon = 'snow'; break;
            case "4": type = 'card-snow';  icon = 'snow'; break;
            case "5": type = 'card-storm'; icon = 'storm'; break;
            default:  type = 'card-night'; icon = 'night'; break;
          }
          let html = '';
          html += `<li class="grid-item">`;
          // html += `    <h3 class="time">${time}</h3>`;
          html += `  <div class="card ${type}">`;
          html += `    <div class="${icon}"></div>`;
          html += `  </div>`;
          html += `  <div class="status">`;
          html += `    <p>${temp}</p>`;
          html += `    <p>${location}</p>`;
          html += `  </div>`;
          html += `</li>`;
          allHTML += html;
      }
      allHTML += `</ul>`;

      return allHTML;
    }
  }, 
  computed:{
    
  }
}

</script>

